
const phrases = [
  'Hello, World!',
  'This is a typing effect.',
  'You can customize the speed and appearance.',
  'Feel free to add more phrases!',
  'Enjoy coding!',
];

const outputElement = document.getElementById('output');

function typeText(text, speed) {
  return new Promise(resolve => {
    let index = 0;
    const intervalId = setInterval(() => {
      if (!outputElement) return
      outputElement.textContent = text.slice(0, index);
      index++;

      if (index > text.length) {
        clearInterval(intervalId);
        setTimeout(resolve, 1000); // Aguarda 1 segundo antes de resolver a Promise
      }
    }, speed);
  });
}

async function typePhrases() {
  for (const phrase of phrases) {
    await typeText(phrase, 100); // Ajuste a velocidade conforme necessÃ¡rio
    await new Promise(resolve => setTimeout(resolve, 1000)); // Aguarda 1 segundo entre as frases
  }
}

typePhrases();
 